'use strict';

import angular from 'angular';
import 'angular-material';
import mainwrap from 'common/directives/mainwrap/mainwrap';
import configService from 'config/config';
import CommercialTermsCtrl from './commercialTerms.controller';
import './commercialTerms.tpl';
import './commercialTerms.css!';

const commercialTermsModule = angular.module('commercialTerms', [
  mainwrap.name,
  configService.name,
  'app/commercialTerms/commercialTerms.tpl.html',
]);

commercialTermsModule.config( ($stateProvider) => {
  $stateProvider
    .state('commercialTerms', {
      url: '/commercial-terms',
      templateUrl: 'app/commercialTerms/commercialTerms.tpl.html',
      controller: CommercialTermsCtrl,
      controllerAs: 'commercialTermsCtrl'
    });
});

export default commercialTermsModule;
